// ==================     Select ==================
//   select      before  after
//         div standart-select__body
//     select
.standart-select {
  &__body {
    position: relative;

    //  
    //  before   select   
    //    
    //   ()     
    &::after {
      content: "";
      position: absolute;
      right: 12px;
      top: 50%;
      display: block;
      width: 32px;
      height: 32px;
      background-image: url("../img/tpl/icons/arrow-drop-down.svg");
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translateY(-50%);
      pointer-events: none;
    }
  }

  &__label {
    display: inline-block;
    margin-bottom: 8px;
    cursor: pointer;
    user-select: none;
  }

  &__select {
    //   
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: 2px solid #81d4fa;
    border-radius: 4px;
    padding: 12px 44px 12px 12px;
    width: 100%;
    font-size: 20px;
    line-height: 1.2;
    background-color: #fff;
    cursor: pointer;

    &:focus {
      outline: 0;
      box-shadow: 0 0 0 1px rgba(5, 135, 241, 0.464) inset;
    }
  }
}

//    ie10, ie11
.standart-select__select::-ms-expand {
  display: none;
}
