.title {
  margin: 0px 0px 20px 0px;
  font-size: 30px;
}

/*    */
.text {
  margin: 0px 0px 80px 0px;
}
/*   */
.text__image {
  margin: 0px 0px 20px 0px;
  text-align: center;
}
/*  */
.text__image img {
  max-width: 100%;
}

.text__value p {
  margin: 0px 0px 20px 0px;
}

/*    */
.cards {
  margin: 0px 0 70px 0;
}
.cards__row {
  margin: 0px -15px 70px -15px;
  display: flex;
}
.cards__column {
  flex: 0 1 33.333%;
  padding: 0px 15px;
  border: 1px solid rgb(58, 58, 58);
  background-color: #f3b9b9;
}

/*   */
.cards__image {
  margin: 0px 0px 20px 0px;
  padding: 0px 0px 58% 0px;
  overflow: hidden;
  position: relative;
}
/*  */
.cards__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

/*    2 */

.items__item {
  display: flex;
  margin: 0px 0px 30px 0px;
}
/*   */
.items__image {
  flex: 0 0 33.333%;
  position: relative;
  min-height: 150px;
}
/*  */
.items__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.items__body {
  flex: 1 1 auto;
  padding: 0px 0px 0px 30px;
}
.items__label {
  font-size: 20px;
  font-weight: 700;
  display: inline-block;
  margin: 0px 0px 20px 0px;
}
