/*   */

:root {
  --container-width: 1200px;
  --container-padding: 20px;
}

.content-demo {
  margin-bottom: 5rem;
  padding: 1rem;
  background-color: #dadada;
}

/*  */
.container {
  margin: 0 auto;
  padding: 0 var(c);

  max-width: var(--container-width);
  width: 100%;
}

.container-full {
  padding: 0 var(--container-padding);
  max-width: 100%;
}

.container-left-50 {
  padding: 0 var(--container-padding);
  max-width: 50%;

  @include mobile {
    max-width: 100%;
  }
}

.container-right-50 {
  padding: 0 var(--container-padding);
  max-width: 50%;
  margin-left: auto;

  @include mobile {
    max-width: 100%;
  }
}

.container-right {
  padding-left: calc(
    (100% - var(--container-width)) / 2 + var(--container-padding)
  );

  @include tablet {
    padding-left: var(--container-padding);
  }
}

.container-left {
  padding-right: calc(
    (100% - var(--container-width)) / 2 + var(--container-padding)
  );

  @include tablet {
    padding-right: var(--container-padding);
  }
}

.container-half-left {
  padding-right: calc(
    (100% - var(--container-width)) / 2 + (var(--container-width) / 2)
  );
  padding-left: calc(
    (100% - var(--container-width)) / 2 + var(--container-padding)
  );

  @include tablet {
    padding-left: var(--container-padding);
  }

  @include mobile {
    padding: 0 var(--container-padding);
  }
}

.container-half-right {
  padding-left: calc(
    (100% - var(--container-width)) / 2 + (var(--container-width) / 2)
  );
  padding-right: calc(
    (100% - var(--container-width)) / 2 + var(--container-padding)
  );

  @include tablet {
    padding-right: var(--container-padding);
  }

  @include mobile {
    padding: 0 var(--container-padding);
  }
}
